.questions {
  &__container {
    position: relative;
  }

  &__content {
    display: flex;
    align-items: center;
    align-items: end;
    overflow: hidden;
    padding-top: 50px;

    @include tablet-830 {
      flex-direction: column;
    }
  }

  &__accordion,
  &__img {
    flex: 0 0 50%;
    width: 50%;
    max-width: 50%;

    @include tablet-830 {
      flex: 1;
      width: 100%;
      max-width: 100%;
    }
  }

  &__accordion {
    padding-right: 50px;
    padding-bottom: 50px;
    align-self: center;

    @include tablet-830 {
      padding-left: 50px;
    }

    @include mobile {
      padding-right: 0;
      padding-left: 0;
    }
  }

  &__accordion.questions__accordion--page {
    flex: 0 0 90%;
    width: 90%;
    max-width: 90%;
    padding-right: 0px;
    margin: 0 auto 100px;
    background-color: var(--grey-color-bg);
    border-radius: 44px;
    padding: 60px 150px;

    @include tablet-830 {
      flex: 1;
      width: 100%;
      max-width: 100%;
      padding: 60px 40px;
    }
  }

  &__img {
    @include tablet-830 {
      display: none;
    }

    img {
      margin-bottom: -1em;
    }
  }

  &__btn {
    padding: 24px 0 0 60px;
  }
}